import React from 'react'
import { useDispatch, useSelector } from 'react-redux'
import styled from 'styled-components'
import MenuComponents from './MenuComponents'
import { CustomMenuButton, Inline } from './ShopMenu'
import { gameStateTransition, incrementWave } from '../gameSlice'
import GameStates, { SubStates } from '../constants/GameStates'
import store from '../../../app/store'
import saveGame from '../utils/saveGame'
import Axios from 'axios'
import { Container } from 'pixi.js'

const { MenuButton, MenuButtonContainer, MenuButtonSpacer } = MenuComponents

const BottomBanner = styled.div`
height: 140px;
width: 100%;
position: absolute;
margin: auto;
top: 40px;
`

const Back = styled.div`
height: 140px;
width: 100%;
background-color: #222;
opacity: .5;
`

export const nextWave = () => {
  let { dispatch, getState } = store
  let { waveNum:wave, lvlToWin, gameState, gameSubState } = getState().game
  let screenUI = window.gameObjects.getKeyValue('screen-ui')
  if (wave < lvlToWin) {
    saveGame()
    screenUI.unpause()
    // dispatch(incrementWave())
    dispatch( gameStateTransition( 'main', gameState, GameStates.GAMEPLAY ) )
    dispatch( gameStateTransition( 'sub', gameSubState, SubStates.WAITING ) )
  } else {
    alert('You win!')
  }
}

export const processScoreInput = (name) => {
  const { getState } = store
  Axios.post('http://localhost:8080/scores', { name, score: getState().game.score })
  .catch(err => console.log(err))
  .then(result => {
    console.log(result)
  })
}

function ScoreSubmitScreen () {

  const [nameInputValue, setNameInputValue] = React.useState('')

  const handleNameInput = e => {
    e.preventDefault()
    setNameInputValue( e.value )
  }

  return (
    <>
      <Container>
        <MenuButtonContainer space-top={2}>
          <Inline>
            <MenuButtonSpacer padding={4}>
              <input onChange={ handleNameInput }></input>
            </MenuButtonSpacer>
          </Inline>
          <Inline>
            <MenuButtonSpacer padding={4}>
              <CustomMenuButton
                width={300}
                height={40}
                onClick={
                  () => {
                    processScoreInput(nameInputValue)
                }}>
                Shop
              </CustomMenuButton>
            </MenuButtonSpacer>
          </Inline>
        </MenuButtonContainer>
      </Container>
    </>
  )

}

export default function ShopQuery () {

  const { gameObjects } = window
  const dispatch = useDispatch()
  const wave = useSelector(state => state.game.waveNum)
  const lvlToWin = useSelector(state => state.game.lvlToWin)
  const gameState = useSelector(state => state.game.gameState)
  const gameSubState = useSelector(state => state.game.gameSubState)
  const [shopQueryMenuState, setQueryMenuState] = React.useState('')

  const gotoShop = () => {
    dispatch( gameStateTransition( 'main', gameState, GameStates.SHOP_MENU ) )
    dispatch( gameStateTransition( 'sub', gameSubState, SubStates.NEUTRAL ) )
  }
  
  const gotoBuildMenu = () => {
    dispatch( gameStateTransition( 'main', gameState, GameStates.BUILD_MENU ) )
    dispatch( gameStateTransition( 'sub', gameSubState, SubStates.NEUTRAL ) )
  }

  const gotoScoreSubmitScreen = () => {
    setQueryMenuState('score submit screen')
  }

  return (
    <>
      {
        shopQueryMenuState === 'score submit screen' ?
        <>
          <ScoreSubmitScreen backToShopQuery={ () => setQueryMenuState('') } />
        </>
        :
        <BottomBanner className='shop-query-menu'>
          <Back />
          <MenuButtonContainer space-top={2}>
            <MenuButtonSpacer>
              <Inline>
                <MenuButtonSpacer padding={4}>
                  <CustomMenuButton width={300} height={40} onClick={ gotoShop }>Shop</CustomMenuButton>
                </MenuButtonSpacer>
              </Inline>
              <Inline>
                <MenuButtonSpacer padding={4}>
                  <CustomMenuButton width={300} height={40} onClick={ gotoBuildMenu }>Build Barriers/Traps</CustomMenuButton>
                </MenuButtonSpacer>
              </Inline>
            </MenuButtonSpacer>
            <MenuButtonSpacer padding={2}>
              <CustomMenuButton width={300} height={40} onClick={ wave < lvlToWin ? nextWave : gotoScoreSubmitScreen }>
                { wave < lvlToWin ? 'Next Wave' : 'Submit your score!' }
              </CustomMenuButton>
            </MenuButtonSpacer>
            <MenuButtonSpacer padding={2}>
              <CustomMenuButton width={300} height={40} onClick={ gotoScoreSubmitScreen }>
                Submit your score
              </CustomMenuButton>
            </MenuButtonSpacer>
          </MenuButtonContainer>
        </BottomBanner>

      }
  </>
  )

}